    data[16] = auc.tax;
    
    var images = _.take(auc.attachments.map(a => a.name), 10);
    
    var index = 73;
    _.forEach(images, image => {
      data[index] = image;
      index += 2;
    });

    this.data.push(_.merge(defaults, data));
