'use strict';

class Util {
  static nl2br(text) {
    return text.replace(/\r\n|\n/g, '<br>');
  }

  static uuid() {
    return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
      var r = Math.random() * 16 | 0;
      var v = c === 'x' ? r : r & 0x3 | 0x8;
      return v.toString(16);
    });
  }
}

module.exports = Util;
